.MenuIcon_MenuIcon_3eSfI {
	position: absolute;
	z-index: 3;
	right: 35px;
	top: 12px;
	cursor: pointer;
}

.MenuIcon_Bar1_2MwrU,
.MenuIcon_Bar2_1Hd5W,
.MenuIcon_Bar3_24xBl {
	width: 35px;
	height: 3px;
	background-color: #fff;
	margin: 6px 0;
	-webkit-transition: 0.4s;
	transition: 0.4s;
	z-index: 10;
	border-radius: 2px;
}

/* Rotate first bar */
.MenuIcon_Change_1IKjH .MenuIcon_Bar1_2MwrU {
	-webkit-transform: rotate(45deg) translate(7px, 7px);
	        transform: rotate(45deg) translate(7px, 7px);
}

/* Fade out the second bar */
.MenuIcon_Change_1IKjH .MenuIcon_Bar2_1Hd5W {
	opacity: 0;
}

/* Rotate last bar */
.MenuIcon_Change_1IKjH .MenuIcon_Bar3_24xBl {
	-webkit-transform: rotate(-45deg) translate(6px, -6px);
	        transform: rotate(-45deg) translate(6px, -6px);
}
