h1,
h2,
h3 {
	margin: 0;
	font-weight: 400;
	color: #fff;
}

.About_About_2XCEX {
	min-width: 360px;
}

.About_AboutInfo_2O1U5 {
	display: grid;
	grid-gap: 30px;
	grid-template-areas: 'bioimage bio bio' 'job1 job1 null';
	grid-template-columns: repeat(3, 1fr);
	padding-bottom: 50px;
}

.About_Bio_h2THn {
	grid-area: bio;
	min-width: 350px;
	font-size: 1.3rem;
}

.About_BioImage_10nkR {
	grid-area: bioimage;
	margin: auto;
}
.About_Job1_FjOqR {
	grid-area: job1;
	min-width: 350px;
	color: #fff;
}

.About_Job_10tft {
	background: #515151;
	padding: 0.5rem;
	border-bottom: #eece1a 5px solid;
}

@media screen and (max-width: 768px) {
	.About_AboutInfo_2O1U5 {
		-webkit-transition: all 0.8s ease-out;
		transition: all 0.8s ease-out;
		grid-template-areas: 'bioimage' 'bio' 'job1';
		grid-template-columns: 1fr;
	}

	.About_About_2XCEX {
		background: #454545;
		-webkit-transition: all 0.8s ease-out;
		transition: all 0.8s ease-out;
	}
}

@media screen and (max-width: 450px) {
	.About_About_2XCEX {
		padding: 0.5rem;
		-webkit-transition: all 0.8s ease-out;
		transition: all 0.8s ease-out;
	}
	.About_BioImage_10nkR {
		margin: 0 auto;
	}
}
